.reviews-section {
  position: relative;
}

.carousel {
  margin-bottom: 60px;
  @media screen and (min-width: 768px) {
  margin-bottom: 49px;
  }
  @media screen and (min-width: 1280px) {
  margin-bottom: 64px;
  }

  &__title {
    font-family: 'Caveat Brush';
    font-size: 24px;
    line-height: 1.25;
    text-align: center;
    color: var(--main-text-color-black);
    margin-bottom: 20px;
    @media screen and (min-width: 768px) {
      margin-bottom: 7px;
    }
    @media screen and (min-width: 1280px) {
      font-size: 36px;
      line-height: 1.25;
      margin-bottom: 85px;
    }
  }
}

