  text-align: center;
  font-style: italic;
  font-size: 14px;
  line-height: 1.21;
  color: var(--main-text-color-black);
  margin-bottom: 8px;
  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 1.25;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.25;
  }
}
&__text {
  display: none;
  text-align: center;
  font-size: 16px;
  line-height: 1.25;
  color: var(--main-text-color-black);
  @media screen and (min-width: 1280px) {
    font-size: 18px;
    line-height: 1.22;
  }
}

