.rating {
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  &__item:not(:last-child) {
    margin-right: 4px;
}
}

.rating__icon {
	-webkit-animation: rotating 30s infinite linear;
	-moz-animation: rotating 30s infinite linear;
	-ms-animation: rotating 30s infinite linear;
	-o-animation: rotating 30s infinite linear;
	animation: rotating 30s infinite linear;
}

@-webkit-keyframes rotating {
	to { -webkit-transform: rotate(360deg); }
}
@-moz-keyframes rotating {
	to { -moz-transform: rotate(360deg); }
}
@-ms-keyframes rotating {
	to { -ms-transform: rotate(360deg); }
}
@-o-keyframes rotating {
	to { -o-transform: rotate(360deg); }
}
@keyframes rotating {
	to { transform: rotate(360deg); }
}

.slider-carousel__text--active {
  display: block;
}

.reviews {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.reviews--animation {
  opacity: 0;
}

.slick-slide img { 
    margin-bottom: 8px
  }


.slick-slider {
  width: 280px;
  margin: 0 auto;

  @media screen and (min-width: 768px) {
    width: 704px;
  }
  @media screen and (min-width: 1280px) {
    width: 1031px;
  }
}



.slick-slide,
.slick-slide[aria-hidden='true']:not(.slick-cloned)
  ~ .slick-cloned[aria-hidden='true'] {
  .slider-carousel__image {
    width: 152px;
  
    @media screen and (min-width: 768px) {
      width: 152px;
    }
    @media screen and (min-width: 1280px) {
      width: 288px;
    }
  }
}

.slick-center,
.slick-slide[aria-hidden='true']:not([tabindex='-1'])
  + .slick-cloned[aria-hidden='true'] {
  .slider-carousel__image {
    width: 280px;
    margin-bottom: 8px;
    @media screen and (min-width: 768px) {
      width: 336px;
      margin-top: 76px;
    }
    @media screen and (min-width: 1280px) {
      width: 394px;
      margin-top: 143px;
    }
  }
}


.reviews-pagination {
  position: relative;
  text-align: center;
  font-size: 14px;
  line-height: 1.21;
  color: var(--accent-color);
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.25;
  }
}

.reviews-pagination::after {
  content: '';
  display: inline-block;
  align-items: center;
  width: 35px;
  height: 1px;
  margin-left: 12px;
  vertical-align: middle;
  background-color: currentColor;
}

.reviews-pagination::before {
  content: '';
  display: inline-block;
  align-items: center;
  width: 35px;
  height: 1px;
  margin-right: 12px;
  vertical-align: middle;
  background-color: currentColor;
}



